import * as React from 'react';

export function useStickynav(
	stickyRef: React.RefObject<HTMLElement>,
	isPost: boolean,
	activeClass: string,
) {
	const [currentClass, setCurrentClass] = React.useState(null);
	let lastScrollY = typeof window !== `undefined` && window.scrollY;
	let ticking = false;

	const update = () => {
		if (stickyRef && stickyRef.current !== null) {
			const top = stickyRef.current.getBoundingClientRect().top;
			const trigger = top + window.scrollY;
			let triggerOffset = -20;

			if (isPost) {
				triggerOffset = stickyRef.current.offsetHeight + 35;
			}

			if (lastScrollY >= trigger + triggerOffset) {
				setCurrentClass(activeClass);
			} else {
				setCurrentClass('');
			}
			ticking = false;
		}
	};
	const requestTick = () => {
		if (!ticking) {
			requestAnimationFrame(update);
		}
		ticking = true;
	};

	const onScroll = () => {
		lastScrollY = window.scrollY;
		requestTick();
		//console.log(lastScrollY);
	};
	const throttledScroll = () => setTimeout(onScroll, 50);

	React.useEffect(() => {
		// Add scroll event when the component is loaded
		window.addEventListener('scroll', throttledScroll, { passive: true });
		return () => {
			// Remove scroll event after the component is unmount,
			// like componentWillUnmount()
			window.removeEventListener('scroll', () => throttledScroll);
		};
		// Here, we set useEffect() arguments as empty array.
		// This means useEffect() will only run once after component loaded
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);
	/* 	const handleScroll = () => {
		// Make sure ref has current attibute and getBoundingClientRect function
		// otherwise, it could cause getBoundingClientRect undefined error.
		if (ref && ref.current && ref.current.getBoundingClientRect()) {
			// Then, we compare the distance of the ref component to the top
			// with top value we set. If less than, we set isStick ture.
			setSticky(ref.current.getBoundingClientRect().top <= top);
		}
  }; */
	return currentClass;
}
