import * as React from 'react';
import BackgroundImage from 'gatsby-background-image';
import { FluidObject, FixedObject } from 'gatsby-image';
import styles from './HeaderBackground.module.scss';
//import * as PropTypes from 'prop-types';

/* const BackgroundSheet = styled.div`
	background-color: #000;
`; */

const HeaderBackground: React.FC<{
	fluidImg?: FluidObject;
	fixedImg?: FixedObject;
	srcImg?: string;
	alt?: string;
}> = ({ fluidImg, fixedImg, srcImg, alt, children }) => {
	return (
		<>
			<div className={styles.siteHeaderBackgroundWrapper}>
				{fluidImg ? (
					<div style={{ backgroundColor: '#000' }}>
						<BackgroundImage
							Tag="div"
							fluid={fluidImg}
							alt={alt}
							preserveStackingContext={false}
							className={
								'outer responsive-header-img' +
								' ' +
								styles.siteHeaderBackground
							}
							backgroundColor={`#000`}
						>
							{children}
						</BackgroundImage>
					</div>
				) : fixedImg ? (
					<div style={{ backgroundColor: '#000' }}>
						<BackgroundImage
							Tag="div"
							fixed={fixedImg}
							alt={alt}
							preserveStackingContext={false}
							className={
								'outer responsive-header-img' +
								' ' +
								styles.siteHeaderBackground
							}
							backgroundColor={`#000`}
						>
							{children}
						</BackgroundImage>
					</div>
				) : srcImg ? (
					<div
						className={
							'outer responsive-header-img' + ' ' + styles.siteHeaderBackground
						}
						style={{ backgroundImage: `url(${srcImg})` }}
					>
						{children}
					</div>
				) : (
					<div
						className={
							'outer responsive-header-img no-image' +
							' ' +
							styles.siteHeaderBackground
						}
					>
						{children}
					</div>
				)}
			</div>
		</>
	);
};

export default HeaderBackground;

