import * as React from 'react';
import Footer from '../Footer/Footer';
import DocumentHead from '../Metadata/DocumentHead';
import styles from './Layout.module.scss';
//import { BodyClass } from '~components/Helpers/Classname';
import Navigation from '~components/Layout/Navigation/NavNew';
import Subscribe from '~components/Layout/Subscribe/Offcanvas/Subscribe';
import FramerWrapper from '~context/Context/FramerContext';

const BodyClass = (template: string): string => {
	const classes = [];

	if (template === 'home') {
		classes.push(`home-template`);
	} else if (template === 'post') {
		classes.push(styles.postTemplate);
	} else if (template === 'page') {
		classes.push(styles.postTemplate);
	} else if (template === 'author') {
		classes.push(`author-template`);
	}

	return classes.join(` `).trim();
};

const Layout: React.FC<LayoutProps> = ({
	children,
	template,
	stickyRef,
	data,
}) => {
	const bodyClass = BodyClass(template);
	return (
		<div>
			<DocumentHead className={bodyClass} template={template} data={data} />
			<div className={styles.siteWrapper}>
				<Navigation template={template} stickyRef={stickyRef} data={data} />
				<FramerWrapper>
					<main
						//ref={template !== 'post' ? stickyRef : null}
						id="site-main"
						className={styles.siteMain}
					>
						{children}
					</main>
				</FramerWrapper>
				<Footer />
				<Subscribe />
			</div>
		</div>
	);
};

export default Layout;

interface LayoutProps {
	template?: string;
	stickyRef?: React.RefObject<HTMLElement>;
	data?: unknown;
	header?: JSX.Element;
}
