import SunIcon from './sun-icon';
import MoonIcon from './moon-icon';
//import { Moon as MoonIcon } from '@styled-icons/fa-solid/Moon';
//import { WbSunny as SunIcon } from '@styled-icons/material-twotone/WbSunny';

import * as React from 'react';
//import useTheme from '~context/Hooks/use-theme';
import { ThemeContext } from '~context/Context/ThemeContext';

export const Switch = ({ theme }) => {
	return theme === 'light' ? <MoonIcon /> : <SunIcon />;
};

const ThemeSwitch: React.FC = () => {
	//const [theme, setTheme] = useTheme();
	const { theme, setTheme } = React.useContext(ThemeContext);
	/* 	console.log(theme);
	React.useEffect(() => {
		console.log('effect', theme);
		return () => {
			console.log('cleanup', theme);
		};
	}, [theme]); */

	if (typeof window == 'undefined') {
		return (
			<div
				className="social-link social-link-fb"
				title="Switch Theme"
				onClick={() => setTheme(theme === 'dark' ? 'light' : 'dark')}
			></div>
		);
	}

	return (
		<div
			className="social-link social-link-fb"
			title="Switch Theme"
			onClick={() => setTheme(theme === 'dark' ? 'light' : 'dark')}
		>
			<Switch theme={theme} />
		</div>
	);
};

export default ThemeSwitch;
