import * as React from 'react';
//import * as PropTypes from 'prop-types';
import { Helmet } from 'react-helmet';
import useIntlHelper from '~components/utils/Intl/IntlHelper';
import { useLocaleContext } from '~context/Hooks/useLocaleContext';
import { SEO } from './Meta/SEO';
import { getMetadata } from './getMetadata';
import { useLocation } from '@reach/router';
import siteImg from '~assets/images/siban-logo.png';

const DocumentHead = ({ className, template, data }) => {
	const { activeLocale } = useLocaleContext();
	const { formatMessage } = useIntlHelper();
	const site = {
		title: formatMessage('meta.title'),
		desc: formatMessage('meta.desc'),
		notFound: formatMessage('notFound.title'),
		image: siteImg,
		url: 'https://sibaniran.com',
		twitter: 'twitter',
		language: activeLocale == 'en' ? 'en' : 'fa',
		locale: activeLocale == 'en' ? 'en_US' : 'fa_IR',
		direction: activeLocale == 'en' ? 'ltr' : 'rtl',
	};

	const seo = getMetadata(data, template, site, formatMessage);
	const { pathname } = useLocation();
	return (
		<>
			<SEO
				title={template === 'home' ? site.title : seo.title}
				titleTemplate={template === 'home' ? site.desc : site.title}
				titleSeparator={` - `}
				description={seo.desc || 'nothing’'}
				image={seo.image}
				pathname={`${site.url}${pathname}`}
				siteLanguage={site.language}
				siteLocale={site.locale}
				siteDirection={site.direction}
				twitterUsername={template === 'author' ? seo.twitter : site.twitter}
				author={template === 'post' ? seo.author : site.title}
				article={template === 'post' ? true : false}
				datePublished={template === 'post' ? seo.date : null}
			/>
			<Helmet>
				<body className={className} />
			</Helmet>
		</>
	);
};

/* DocumentHead.propTypes = {
	site: PropTypes.object.isRequired,
	className: PropTypes.string.isRequired,
};
 */
export default DocumentHead;
