import * as React from 'react';
import siteLogo from '~assets/images/ghost-logo.png';
//import indexCover from '~assets/images/publication-cover.png';
import useIntlHelper from '~components/utils/Intl/IntlHelper';
import Link from '~components/utils/Link/Link';
import SiteNav from '../Navigation/SiteNav/SiteNav';
import HeaderBackground from './HeaderBackground';
import styles from './Header.module.scss';
import AvatarIcon from '~assets/icons/avatar-icon';

const IndexHero = () => {
	const { formatMessage } = useIntlHelper();
	const site = {
		title: 'general.header',
		description: 'general.headerSubtitle',
		logo: siteLogo,
		logoOld:
			'https://demo.ghost.io/content/images/size/w1000/2014/09/Ghost-Transparent-for-DARK-BG.png',
	};
	return (
		<HeaderBackground
		//fluidImg={data.indexCover.childImageSharp.fluid}
		//fixedImg={data.indexCover.childImageSharp.fixed}
		//srcImg={indexCover}
		>
			<div className="inner">
				<SiteNav className="site-nav" />
				<div className="site-header-content">
					<h1 className="site-title">
						{site.logo ? (
							<Link to="/">
								<img
									className={styles.siteLogo}
									src={site.logo}
									alt={formatMessage(site.title)}
								/>
								{/* 			<div className={styles.siteLogoSVG}>
											<Logo />
									</div> */}
							</Link>
						) : (
							<Link to="/">{formatMessage(site.title)}</Link>
						)}
					</h1>
					<h2 className="site-description">
						{formatMessage(site.description)}
					</h2>
				</div>
			</div>
		</HeaderBackground>
	);
};

const AuthorHero = ({ author, posts }) => {
	const { formatMessage } = useIntlHelper();

	const twitterUrl = author.twitter
		? `https://twitter.com/${author.twitter.replace(/^@/, ``)}`
		: null;
	const facebookUrl = author.facebook
		? `https://www.facebook.com/${author.facebook.replace(/^\//, ``)}`
		: null;
	return (
		<HeaderBackground fluidImg={author.cover.childImageSharp.fluid}>
			<div className="inner">
				<div className="site-header-content author-header">
					{author.avatar ? (
						<img
							className="author-profile-image"
							src={author.avatar.childImageSharp.fluid.src}
							alt={author.name}
						/>
					) : (
						<div className="author-profile-image">
							<AvatarIcon />
						</div>
					)}
					<div className="author-header-content">
						<h1 className="site-title">
							{formatMessage(`authors.${author.id}.title`)}
						</h1>
						{author.bio && (
							<h2 className="author-bio">
								{formatMessage(`authors.${author.id}.bio`)}
							</h2>
						)}
						<div className="author-meta">
							{author.location && (
								<div className="author-location">
									{formatMessage(`authors.${author.id}.location`)}
								</div>
							)}
							<div className="author-stats">
								{(posts.edges.length &&
									` ${posts.edges.length} post${
										1 < posts.edges.length && `s`
									}`) ||
									`No posts`}
							</div>
							{author.website && (
								<span className="author-social-link">
									<a
										href={author.website}
										target="_blank"
										rel="noopener noreferrer"
									>
										Website
									</a>
								</span>
							)}
							{twitterUrl && (
								<span className="author-social-link">
									<a
										href={twitterUrl}
										target="_blank"
										rel="noopener noreferrer"
									>
										Twitter
									</a>
								</span>
							)}
							{facebookUrl && (
								<span className="author-social-link">
									<a
										href={facebookUrl}
										target="_blank"
										rel="noopener noreferrer"
									>
										Facebook
									</a>
								</span>
							)}
						</div>
					</div>
				</div>
			</div>
		</HeaderBackground>
	);
};
const TagHero = ({ data }) => {
	const { formatMessage } = useIntlHelper();
	return (
		<div className="site-archive-header">
			<HeaderBackground fluidImg={data?.tag?.image?.childImageSharp.fluid}>
				<div className="inner site-header-content">
					<h1 className="site-title">
						{formatMessage(`tags.${data.tag.id}.title`)}
					</h1>
					<h2 className="site-description">
						{(data.tag.description &&
							formatMessage(`tags.${data.tag.id}.desc`)) ||
							`A collection of ${
								(data.posts.edges.length > 0 &&
									(data.posts.edges.length === 1
										? `1 post`
										: `${data.posts.edges.length} posts`)) ||
								`posts`
							}`}
					</h2>
				</div>
			</HeaderBackground>
		</div>
	);
};

const PostHero = () => {
	return <div></div>;
};
export { IndexHero, PostHero, AuthorHero, TagHero };
