import * as React from 'react';
import { useStickynav } from './SiteNav/useStickynav';
import { Author } from '~templates/__generated__/Author';
import { Post } from '~templates/__generated__/Post';
import { Tag } from '~templates/__generated__/Tag';
import MainNavigation from './Navigation';
import { AuthorHero, IndexHero, TagHero } from '../Headers/Hero';

function getTemplateClass(template: string) {
	if (template === 'home') {
		return 'site-home-header';
	} else if (template === 'post') {
		return 'site-header';
	} else if (template === 'page') {
		return 'site-header';
	} else if (template === 'author') {
		return 'site-archive-header';
	}
}
const activeClass = template =>
	template == 'home' ? 'fixed-nav-active' : 'nav-post-title-active';
const HeaderNew: React.FC<HeaderProps> = ({ template, stickyRef, data }) => {
	const headerClass = getTemplateClass(template);
	const currentClass = useStickynav(stickyRef, false, activeClass(template));

	if (template === 'home') {
		return (
			<>
				<header className={headerClass}>
					<IndexHero />
				</header>
				<MainNavigation currentClass={currentClass} />
			</>
		);
	} else if (template === 'post') {
		return (
			<header className={headerClass}>
				<MainNavigation
					currentClass={currentClass}
					postTitle={data.post.frontmatter.title}
				/>
			</header>
		);
	} else if (template === 'page') {
		return (
			<header className="site-header">
				<MainNavigation />
			</header>
		);
	} else if (template === 'author') {
		return (
			<header className="site-archive-header">
				<MainNavigation />
				<AuthorHero author={data.author} posts={data.posts} />
			</header>
		);
	} else if (template === 'tag') {
		return (
			<header className="site-archive-header">
				<MainNavigation />
				<TagHero data={data} />
			</header>
		);
	} else if (template === 'notfound') {
		return (
			<header className="site-header">
				<MainNavigation />
			</header>
		);
	}
};

export default HeaderNew;

interface HeaderProps {
	template: string;
	stickyRef?: React.RefObject<HTMLElement>;
	data?: Post | Author | Tag | unknown;
}
