import * as React from 'react';
import useIntlHelper from '~components/utils/Intl/IntlHelper';
import Navigation from './Items/Navigation';
import SocialLinks from './Items/SocialLinks';
import ThemeSwitch from '~assets/icons/theme-switch';
import Link from '~components/utils/Link/Link';
import navLogo from '~assets/images/ghost-logo.png';
//import Logo from '~assets/logo/logo';
interface SiteNav {
	className?: string;
	postTitle?: string;
}

const site = {
	title: 'Static Title',
	logo: navLogo,
};

const SiteNav: React.FC<SiteNav> = ({ className, postTitle }) => {
	const { formatMessage } = useIntlHelper();

	const navigation = [
		{
			label: formatMessage('navigation.home'),
			url: '/',
		},
		{
			label: formatMessage('navigation.news'),
			url: '/tags/news/',
		},
		{
			label: formatMessage('navigation.products'),
			url: '/products/',
		},
		{
			label: formatMessage('navigation.catalogue'),
			url: '/catalogue',
		},
		{
			label: formatMessage('navigation.companies'),
			url: '/companies',
		},
		{
			label: formatMessage('navigation.about'),
			url: '/about',
		},
	];
	return (
		<nav className={className}>
			<div className="site-nav-left-wrapper">
				<div className="site-nav-left">
					{site.logo ? (
						<Link className="site-nav-logo" to="/">
							{/* <Logo /> */}
							<img src={site.logo} alt={site.title} />
						</Link>
					) : (
						<Link className="site-nav-logo" to="/">
							{formatMessage('general.header')}
						</Link>
					)}
					<div className="site-nav-content">
						<Navigation data={navigation} />

						{postTitle && (
							<span className={`nav-post-title ${site.logo || `dash`}`}>
								{postTitle}
							</span>
						)}
					</div>
				</div>
			</div>
			<div className="site-nav-right">
				<div className="social-links">
					<ThemeSwitch />
					<SocialLinks />
				</div>
				<>
					<a className="subscribe-button" href="#subscribe">
						{formatMessage('navigation.subscribe')}
					</a>
				</>
			</div>
		</nav>
	);
};

export default SiteNav;
